.goBackBtn {
  padding: 15px 25px;
  border-radius: 10px;
  text-transform: uppercase;
  background-color: darkslateblue;
  color: white;
  font-family: inherit;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 700;
}
.goBackBtn:hover {
  background-color: rgb(41, 33, 94);
}
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 100vh;
  gap: 20px;
  background-image: url('../../assets/notFound_bg.gif');
  background-size: 100vh;
  background-repeat: no-repeat;
  mix-blend-mode: multiply;
  background-position: center;
  text-align: center;
}
.container > h2 {
  font-size: 40px;
}
.container > div p {
  max-width: 200px;
}
.container > div {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}
